@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;700&family=M+PLUS+Rounded+1c:wght@500&family=Orbitron&family=Quantico&family=Teko:wght@300&display=swap");
* {
  font-family: Comfortaa, Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  text-decoration: none;
  overflow: auto;
}

#App {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease-in-out;
  position: relative;
}

#quote_box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 400px;
  min-height: 200px;
  height: auto;
  padding: 10px;
  backdrop-filter: blur(19px) saturate(180%);
  -webkit-backdrop-filter: blur(19px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.541);
  border-radius: 12px;
}

#quote {
  text-align: start;
  font-weight: bold;
  font-size: 1rem;
}

#author {
  display: block;
  text-align: end;
  font-style: italic;
  font-weight: bold;
  font-size: 0.9rem;
  padding: 0px 2px;
}

#btns {
  transition: 0.2s ease-in-out;
}

#btns button {
  transition: 0.2s ease-in-out;
  min-width: 75px;
  height: 35px;
  font-weight: bold;
  border: none;
  outline: none;
  margin: 5px;
  padding: 2px 5px;
  cursor: pointer;
  border-radius: 5px;
}

#btns button:hover {
  transform: scale(1.1);
  border: 2px solid skyblue;
}

.spinner-loader {
  width: 12px;
  height: 12px;
  border: 2px solid #232222;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 0.8s linear infinite;
}

@keyframes rotation {
  0% {
    border: 3px solid #656367;
    border-bottom-color: transparent;
    transform: rotate(0deg);
  }
  50% {
    border: 3px solid #656367;
    border-bottom-color: transparent;
  }
  100% {
    border: 3px solid #656367;
    border-bottom-color: transparent;
    transform: rotate(360deg);
  }
}

#prince {
  position: absolute;
  text-align: center;
  bottom: 0px;
  font-size: 0.8rem;
  font-weight: bold;
}

@media (max-width: 550px) {
  #quote_box {
    width: 85%; 
  }
}
